import React, { useState } from 'react';
import FormBuiGenView from './Views/FormBuiGenView';
import Publish from './Views/Publish';
import Design from './Views/Design';

export default function Formbuilder() {
  const [step, setstep] = useState('build');
  const setStep = (step) => setstep(step);
  const getCurrentStep = () => {
    if (step === 'build') return <FormBuiGenView setStep={setStep} />;
    if (step === 'desgin') return <Design setStep={setStep} />;
    if (step === 'publish') return <Publish setStep={setStep} />;
  };
  return (
    <div>
      <div style={{ display: 'flex', justifyContent: 'center' }}>
        <button onClick={() => setStep('build')}>Build</button>
        <button onClick={() => setStep('desgin')}>Desgin</button>
        <button onClick={() => setStep('publish')}>Publish</button>
      </div>
      {getCurrentStep()}
    </div>
  );
}
