import React from 'react';
import { Link } from 'react-router-dom';

const DraftsurveyItems = ({ survey }) => {
  return (
    <div>
      <table class="table">
        <thead>
          <tr>
            <th scope="col">Title</th>
            <th scope="col">Created</th>
            <th scope="col">Last Update</th>
          </tr>
        </thead>
        <tbody>
          {console.log(survey)}
          {survey.map((s) => (
            <tr>
              <td>{s.title}</td>
              <td>{s.created}</td>
              <td>{s.lastupdate}</td>
              <td>
                <Link to="view">View</Link>
              </td>
              <td>
                <Link to="edit">edit</Link>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default DraftsurveyItems;
