import React, { useState } from 'react';
import DraftSurveyItems from './DraftsurveyItems';

const DraftSurveys = () => {
  const [survey, setsurvey] = useState([
    {
      title: 'survey1',
      created: 'survey 1 created',
      lastupdate: 'survey 1 update',
    },
    { title: 'survey2', created: 'survey 2', lastupdate: 'survey 2' },
    {
      title: 'survey3',
      created: 'survey 3',
      lastupdate: 'survey 3',
    },
  ]);
  return (
    <div>
      <DraftSurveyItems survey={survey} />
    </div>
  );
};

export default DraftSurveys;
